import React, { useState, useEffect } from "react" ;

function Films () {

    const [films, setFilms] = useState([]);
    useEffect (() => {
        getFilms()
    },[])

    const getFilms = () => {
        fetch("http://localhost8080/server/films")
        .then(response => response.json())
        .then(films => {
            setFilms(films)
        })

    }
    return (
        <div>
            Films
        </div>
    )
};

export default Films;